@import "styles/variables.scss";

.menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: $darkBlue;
    padding: 0 24px;
    z-index: 100001;
    max-height: 100vh;
    overflow-y: scroll;
}

.menu > :first-child {
    position: absolute!important;
    top: 12px;
    right: 12px;
}

.content {
    margin-top: 40px;
}