@import "styles/variables.scss";

.categories {
    display: flex;
    align-items: center;
}

.categories img {
    cursor: pointer;
}

.categories button {
    border: 1px solid rgba(35, 35, 35, 0.3);
}

.categories button:not(:last-child) {
    margin-right: 5px;
}

.categories > .catigoryButtonGroup {
    max-width: 500px;
    overflow: hidden;
    box-sizing: border-box;
}

.categories > .catigoryButtonGroup > div {
    display: flex;
    width: fit-content;
}

.attributes {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 24px;
}

.dimensions {
    display: flex;
    margin-bottom: 16px;
}

.dimensions > div {
    max-width: 630px;
}

.dimensions > p {
    margin-right: 24px;
    width: 60px;
}

.dimensions button {
    background-color: $beige;
    opacity: 0.4;
    cursor: pointer;
}

.dimensions button:not(:last-child) {
    margin-right: 16px;
}

.colors {
    display: flex;
}

.colors > div {
    display: flex;
    flex-wrap: wrap;
    max-width: 630px;
    row-gap: 4px;
}

.colors > p {
    margin-right: 24px;
    width: 60px;
}

.colors button {
    background-color: $beige;
    opacity: 0.4;
    cursor: pointer;
}

.colors button:not(:last-child) {
    margin-right: 16px;
}

.attributes > :last-child {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.attributes > :last-child > button {
    background-color: $beige;
    cursor: pointer;
}

.attributes > :last-child > button:not(:last-child) {
    margin-bottom: 8px;
}

@media #{$tablet} {
    .filtersCol {
        display: none;
    }
}