@import "styles/variables.scss";

.productsCards {
    display: flex;
    flex-wrap: wrap;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    border-left: 1px solid rgba(35, 35, 35, 0.3);
}

.productsCardsWithoutBT {
    border-top: none;
}

.productsCardsWithoutBT > div {
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.withThree > div {
    margin-bottom: 0;
}

.withThree {
    margin-bottom: 60px;
}

.empty {
    font-size: 46px;
    line-height: 110%;
    letter-spacing: -0.03em;
    width: 100%;
    text-align: center;
}

@media #{$tablet} {
    .productsCards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .empty {
        font-size: 32px;
        letter-spacing: -0.02em;
    }
}