@import "styles/variables.scss";

.actions {
    display: flex;
    justify-content: space-between;
}

.actions > button:last-child {
    background-color: $beige;
    text-transform: uppercase;
    cursor: pointer;
    text-decoration: underline;
}

@media #{$tablet} {
    .actions > button:last-child {
        font-size: 16px;
    }
}