@import "styles/variables.scss";

.filtersCol {
    display: none;
}

.categories {
    display: flex;
    flex-wrap: nowrap;
    width: calc(100vw - 25px);
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.categories::-webkit-scrollbar {
    display: none;
    -webkit-appearance: none;
    width: 0;
    height: 0;
}

.categories > button:not(:last-child) {
    margin-right: 5px;
}

.categories > button:not(:disabled):hover {
    background-color: $beige;
}

.categories > button:not(:disabled):hover > pre {
    color: $darkBlue;
}

.categories > button.whiteText:hover > pre {
    color: $beige;
}

.attributes {
    display: flex;
    margin-top: 16px;
}

.attributes > button {
    width: 100%;
}

.attributes > button:last-child {
    border-left: none;
}

.attributes > button.isSearch {
    border-left: 1px solid #1B2530;
}

.dimensions > p {
    margin-bottom: 16px;
    color: $beige;
    font-size: 16px;
}

.sort {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.sort > button,
.colors button,
.dimensions button {
    text-transform: uppercase;
    margin-right: 16px;
    margin-bottom: 2px;
    background-color: $darkBlue;
    color: $beige;
    font-size: 16px;
    opacity: 0.4;
}

.sort > button {
    margin-bottom: 4px;
    text-transform: none;
}

.colors button {
    text-transform: none;
}

button.whiteText > pre {
    color: #F3F3EA;
}

@media #{$tablet} {
    .filtersCol {
        display: block;
    }
}