  @import "styles/variables.scss";

.banner {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: auto;
    border: 1px solid rgba(35, 35, 35, 0.3);
    padding: 24px;
    cursor: pointer;
}

.banner > div {
    display: flex;
    flex-direction: column;
    justify-content: inherit;
}

.banner a {
    font-size: 80px;
    max-width: 680px;
    letter-spacing: -0.03em;
    text-decoration: none;
}

@media #{$desc} {
    .banner a {
        font-size: 56px;
    }
}

@media #{$tablet} {
    .banner {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 0.5fr;
        padding: 13px;
        width: calc(100% - 26px);
    }

    .banner > div {
        grid-row-start: 2;
        grid-row-end: 3;
    }

    .banner h1 {
        margin-top: 24px;
        margin-bottom: 80px;
    }

    .bannerImg {
        width: 100%;
        height: min-content!important;
    }
}

@media #{$small} {
    .banner a {
        font-size: 46px;
    }
}